.nav {
    z-index: 1;
    position: fixed;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 10px 600px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px
    rgba(0, 0, 0, 0.3),
    0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
        position: fixed;
        top: 0;
    }
    
    header {
        margin-top: 80px;
    }

@media screen and (max-width: 1090x) {
    .nav {
        left: initial;
        top: initial;
        bottom: 0px;
        width: 100%;
    }
}

.nav a:not(:last-child) {
    margin-bottom: 20px;
}

.nav a {
    
    text-decoration: none;
    color: #0008fc;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
}